export const projectsApi = {
  create: (data: CreateProjectRequest) =>
    fetcher<ProjectApiResponse>(API_ENDPOINTS.PROJECTS, {
      method: "POST",
      body: data,
    }),

  // Add other project-related endpoints here
  getAll: (query?: GetAllProjectsQuery) =>
    fetcher<ProjectApiResponse>(API_ENDPOINTS.PROJECTS, {
      method: "GET",
      query,
    }),

  getById: (id: string) =>
    fetcher<ProjectApiResponse>(`${API_ENDPOINTS.PROJECTS}/${id}`),

  update: (id: string, data: Partial<CreateProjectRequest>) =>
    fetcher<Project>(`${API_ENDPOINTS.PROJECTS}/${id}`, {
      method: "PUT",
      body: data,
    }),

  delete: (id: string) =>
    fetcher<void>(`${API_ENDPOINTS.PROJECTS}/${id}`, {
      method: "DELETE",
    }),
};

